const API_KEY = '21313358-1d6d5867e5dc6c68143bd5df5';
const BASIC_URL = 'https://pixabay.com/api/';


function fetchImage(searchQuery, pageNumber) {
    return fetch(`${BASIC_URL}?image_type=photo&orientation=horizontal&q=${searchQuery}&page=${pageNumber}&per_page=12&key=${API_KEY}`).then(r => {
        if (!r.ok) {
            throw Error;
        } else {
            return (r.json());
        }
    });
}

export default { fetchImage };